import {
  EnhancedCell,
  EnhancedElementBlock,
  EnhancedListBlock,
  EnhancedListEntry,
  EnhancedRow,
  EnhancedTableBlock,
  EnhancedTextBlock,
} from "../types/enhance-document.types"

export const isEnhancedTextBlock = (block: EnhancedElementBlock): block is EnhancedTextBlock => {
  return !!(block as EnhancedTextBlock).text
}

export const isEnhancedListBlock = (block: EnhancedElementBlock): block is EnhancedListBlock => {
  return !!(block as EnhancedListBlock).entries
}

export const isEnhancedContent = (
  block: EnhancedElementBlock,
): block is EnhancedListEntry | EnhancedCell => {
  return !!(block as EnhancedListEntry | EnhancedCell).content
}

export const isEnhancedTableBlock = (block: EnhancedElementBlock): block is EnhancedTableBlock => {
  return !!(block as EnhancedTableBlock).rows
}

export const isEnhancedRow = (block: EnhancedElementBlock): block is EnhancedRow => {
  return !!(block as EnhancedRow).cells
}
